import { Component, OnInit, ElementRef } from '@angular/core';
import {  ViewChild } from '@angular/core';
import {AuthenticationService} from '../../services/authentication.service';


declare var jquery:any;
declare var $ :any;

import * as M from "materialize-css/dist/js/materialize";


import {GetdataService} from '../../services/getdata.service';
import {DeletedataService} from '../../services/deletedata.service';
import {AdddataService} from '../../services/adddata.service';
import {RouterModule,ActivatedRoute, Routes} from '@angular/router';

import {Http, Response, Headers} from '@angular/http';
import { MatSnackBar } from '@angular/material';
@Component({
  selector: 'app-customrssback',
  templateUrl: './customrssback.component.html',
  styleUrls: ['./customrssback.component.css']
})
export class CustomrssbackComponent implements OnInit {

  public submitted:boolean;

  user:Object;
  sortedData;
  feed_data;
    constructor(private snackBar:MatSnackBar, private authService:AuthenticationService,  private route: ActivatedRoute,private getdataService:GetdataService,  private deletedataService:DeletedataService, private adddataService:AdddataService, private http:Http, private elem: ElementRef) {
      
    }




ngOnInit() {

   this.submitted=true;
 this.refreshEverything();
}




deleteFeed = function(deletedFeedID) {
           if(confirm('Are you sure you want to delete feed?')) {
           this.submitted=true;
        
           this.deletedataService.deleteFeed(deletedFeedID).subscribe((res:Response)=>{

                 ////////refresh category /////////////
                 this.refreshEverything();
               //////////end refresh category.////////
                this.submitted=false;
         });
      }
    }



  refreshEverything = ()=>{
    this.getdataService.getFeeds().subscribe((data) => {
        console.log(data)
        this.feed_data = data

    },
    err=>{
      ////console.log(err);
      return false;
    });

    

    }



  public addFeed(): void{
    this.submitted=true;
    let files = this.elem.nativeElement.querySelector('#image').files;
    let title = this.elem.nativeElement.querySelector('#title').value;
    let description = this.elem.nativeElement.querySelector('#description').value;
    let formData = new FormData();
    let file = files[0];
    console.log(title, description)
    formData.append('title',title);
    formData.append('description',description);


      console.log(file.name)
      formData.append('image',file,file.name);
      formData.append('using_default_logo','false');
      if(file.size < 10096865)
      {
        this.adddataService.addFeed(formData).subscribe
        (res =>{
       
             this.refreshEverything();
             this.snackBar.open('Feed Uploaded Successfully', 'OK', {
              duration: 3000
            });
            this.submitted=false;
      } );

      }else{
      //  $(window).scrollTop(0);
        this.submitted=false;
        this.snackBar.open('File max size exceeded (10 mb)', 'OK', {
          duration: 3000
        });
      }
    


  }
}


