import { Component, OnInit } from '@angular/core';
import { GetdataService } from '../../services/getdata.service';

@Component({
  selector: 'app-customrssfront',
  templateUrl: './customrssfront.component.html',
  styleUrls: ['./customrssfront.component.css']
})
export class CustomrssfrontComponent implements OnInit {

  feed_data= [];
  counter=0;

  constructor(private getdataService:GetdataService) {
    setInterval(() => {
      this.refreshData();
    }, 7000);
   }

  ngOnInit() {
      this.refreshData();

  }

  refreshData(){
    this.getdataService.getFeeds().subscribe((data) => {
      console.log(data)
      this.feed_data = data
        if(this.counter<data.length)
        {
          console.log(this.counter)
          this.feed_data[0] = data[this.counter];
          this.counter++;
        }else{
          this.counter=0;
        }
      },
      err=>{
        ////console.log(err);
        return false;
      });
  }

}
